.ticket-card {
    display: flex;
    width: 100%
  }
  
  .ticket-card #link {
    display: flex;
    width: 100%;
  }
  
  .ticket-card #link > * {
    background-color: rgb(255, 255, 255);
    margin: 2px;
    padding: 30px;
    width: 100%;
    display: flex;
    align-items: center;

  }
  
  .link-no-underline {
    text-decoration: none; /* Remove underline */
  }
  
  .ticket-card .ticket-color {
    width: 15px;
    margin: 2px;
  }
  
  .ticket-card .avatar-container {
    display: flex;
    justify-content: center;
    width: 20px;
  }
  
  .ticket-card .img-container {
    height: 60px;
    width: 70px;
    border-radius: 25px;
    overflow: hidden;
  }
  
  .ticket-card .img-container img {
    width: 100%;
  }
  
  .ticket-card .status-display {
    display: flex;
    justify-content: center;
  }
  
  .ticket-card .priority-display {
    display: flex;
    justify-content: center;
  }
  
  
  .ticket-card .priority-display .star-container {
    display: flex;
   
  }
  
  .ticket-card .priority-display .star-container h3 {
    margin: 7px;
    padding: 0;
    color: rgb(0, 0, 0);

  }
  
  .ticket-card .progress-display {
    min-width: 200px;
  }
  
  .ticket-card .delete-block {
    background-color: rgb(255, 255, 255);
    width: 50px;
    margin: 2px;
    display: flex;
    justify-content:center;
    align-items: center;
  }
  
  .ticket-card .delete-block .delete-icon {
    width: 20px;
    height: 20px;
    border-radius: 10px;
    background-color: rgb(61, 61, 61);
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255,255,255);
  }
  
  .ticket-card .progress-bar {
    width: 100%;
    height: 30px;
    background-color: rgb(158, 158, 158);
    border-radius: 15px;
    overflow: hidden;
  }
  
  
  .ticket-card  .progress-bar .progress-indicator {
    background-color: rgb(0, 255, 191);
    height: 100%;
  }
  
  /* ----- Ticket ------ */
  
  .ticket {
    display: flex;
    flex-direction: column; /* Stack items vertically */
    height: 100vh; /* Full viewport height */
    background-image: linear-gradient(90deg, #000000, #2C272E);
  }
  
  .ticket h1 {
    text-align: center;
    margin-top: 20px;
    font-size: 2em;
    color: #333; /* Ensure a visible color */
  }

  .ticket .ticket-container {
  flex: 1; /* Take remaining space */
  display: flex;
  justify-content: center; /* Center content horizontally */
  align-items: flex-start; /* Align items at the start */
  padding: 20px; /* Add padding around the ticket form */
}

  .ticket form  {
    display: flex;
    background-color: rgb(0, 0, 0);
    color: white;
  }
  
  .ticket form section {
    display: flex;
    flex-direction: column;
    margin: 10px;
    width: 500px;
  }
  
  .ticket form label {
    margin: 20px 0 0 0;
  }
  
  .ticket form input, select {
    padding: 10px;
    font-size: 15px;
    border-radius: 10px;
    border: 1.5px solid rgb(226, 226, 226);
    margin: 5px;
  }
  
  .ticket form .multiple-input-container {
    display: flex;
    margin: 20px 0 20px 0;
  }
  
  .ticket .img-preview {
    margin: 20px;
    width: 200px;
    height: 200px;
    border-radius: 100px;
    overflow: hidden;
  }
  
  .ticket .img-preview img {
    width: 100%;
  }